import React from 'react'
import styled, { css } from 'styled-components'

import { useAuthorization } from '@resultadosdigitais/front-hub/react'
import { Badge } from '@resultadosdigitais/tangram-components'
import { Button as ButtonStyle, Avatar as AvatarStyle, Text } from '@resultadosdigitais/tangram-components'
import { CaretDown as CaretIcon } from '@resultadosdigitais/tangram-react-icons'
import { Navbar } from '@resultadosdigitais/crm-components'
import { openOnboardingFlowByRole, recalculateUserValue, openUpdateMessagesModal } from '@resultadosdigitais/crm-core'

import { useFronthubContext } from '../../services/context'
import { useTranslation } from '../../services/formatter'
import Routes from '../../routes'
import TotalSales from '../TotalSales'

const Button = styled(ButtonStyle)`
  position: relative;
  max-width: 100%;

  text-align: left;
  display: flex;
  align-items: center;
  padding: var(--size-spacing-03) var(--size-spacing-04) !important;
  background: transparent;
  border-radius: 0;
  border-left: 1px solid var(--color-gray-20);
  transition-duration: var(--motion-duration-extra-fast);

  &:hover {
    cursor: pointer;
    background: transparent;
    outline: none;
  }

  ${props =>
    props['data-active'] &&
    css`
      background: var(--color-gray-10) !important;
      border-color: var(--color-gray-10);
    `}
`

const Avatar = styled(AvatarStyle)`
  min-width: 40px;

  &:hover {
    color: var(--color-gray-70);
  }

  img {
    height: 100%;
    object-fit: fill;
    object-position: center;
  }
`

const Info = styled.div`
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  margin: 0 var(--size-spacing-03);
  max-width: 124px;

  * {
    flex: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  &:hover {
    color: var(--color-gray-90);
  }
`

const Name = styled(Text)`
  color: var(--color-gray-90);
  font: var(--font-size-sm-semibold);

  :hover,
  :active {
    color: var(--color-gray-100);
  }
`

const Path = styled(Text)`
  --color-gray-70: #7e8a98;

  color: var(--color-gray-70);
  font: var(--font-size-xs-semibold);

  :hover,
  :active {
    color: var(--color-gray-80);
  }
`
const WhatsNewItem = ({ unreadCount, children }) => {
  return parseInt(unreadCount) ? <Badge content={unreadCount}>{children}</Badge> : children
}

const AccountMenu = styled(props => {
  const { user = { name: '', account: { monthTotalSales: 0 } }, instance = {}, update, updateMessages = {} } = useFronthubContext()
  const { t: translate } = useTranslation()
  const { Allowed } = useAuthorization()

  const handleRefreshClick = () => {
    recalculateUserValue(user => {
      const monthTotalSales = user.current_user_statistic.total_sales
      update(prevContext => ({
        ...prevContext,
        user: {
          ...prevContext.user,
          account: {
            ...prevContext.user.account,
            monthTotalSales,
          },
        },
      }))
    })
  }

  return (
    <Routes>
      {({ routes }) => (
        <Navbar.List {...props} aria-label="account-menu">
          <Navbar.List.Item>
            <Navbar.Dropdown position={Navbar.Dropdown.position.RIGHT}>
              <Navbar.Dropdown.Trigger id="account-menu">
                <Button>
                  <Avatar alt={user.name} size={Avatar.sizes.SM} src={user.icon} colors={Avatar.colors.COLOR_YELLOW_20} />
                  <Info>
                    <Name as="span">{user.name}</Name>
                    <Path as="span">{instance.path}</Path>
                  </Info>
                  <CaretIcon />
                </Button>
              </Navbar.Dropdown.Trigger>
              <Navbar.Dropdown.List>
                <TotalSales value={Number(user.account.monthTotalSales)} onRefreshClick={handleRefreshClick} />
                <Navbar.Dropdown.Divider />
                <Allowed permission="accountMenu:account">
                  <Navbar.Dropdown.Item href={routes.accounts}>{translate('account')}</Navbar.Dropdown.Item>
                </Allowed>
                <Navbar.Dropdown.Item href={routes.settingsProfile}>{translate('profile')}</Navbar.Dropdown.Item>
                <Navbar.Dropdown.Divider />
                <Navbar.Dropdown.Item href={routes.settings}>{translate('settings')}</Navbar.Dropdown.Item>
                <Allowed permission="accountMenu:integration">
                  <Navbar.Dropdown.Item href={routes.settingsMarketing}>{translate('integration')}</Navbar.Dropdown.Item>
                </Allowed>
                <Navbar.Dropdown.Item onClick={openUpdateMessagesModal}>
                  <WhatsNewItem unreadCount={updateMessages.unreadCount}>{translate('whats_new')}</WhatsNewItem>
                </Navbar.Dropdown.Item>
                <Navbar.Dropdown.Divider />
                <Navbar.Dropdown.Item onClick={openOnboardingFlowByRole}>{translate('first_steps_guide')}</Navbar.Dropdown.Item>
                <Navbar.Dropdown.Divider />
                {user.impersonated && <Navbar.Dropdown.Item href={routes.authImpersonate}>{translate('go_back_to_admin')}</Navbar.Dropdown.Item>}
                <Navbar.Dropdown.Item href={routes.authLogout}>{translate('logout')}</Navbar.Dropdown.Item>
              </Navbar.Dropdown.List>
            </Navbar.Dropdown>
          </Navbar.List.Item>
        </Navbar.List>
      )}
    </Routes>
  )
})`
  width: 240px;
`

export default AccountMenu
