function getCurrencyByLang(lang) {
  const currencies = {
    'pt-BR': 'BRL',
    'en-US': 'USD',
  }
  return currencies[lang]
}

export function format(lang, value) {
  const options = { style: 'currency', currency: getCurrencyByLang(lang) }
  return new Intl.NumberFormat(lang, options).format(value)
}
