import React from 'react'
import styled from 'styled-components'
import PropTypes from 'prop-types'

import { Refresh } from '@resultadosdigitais/tangram-react-icons'
import { Button } from '@resultadosdigitais/tangram-components'

import { useTranslation } from '../../services/formatter'

const Form = styled(({ className, value, onRefreshClick }) => {
  const { t } = useTranslation()

  return (
    <form className={className}>
      <output>{t('total_sales', { value })}</output>
      <Button kind="tertiary" onClick={onRefreshClick} aria-label="sales refresh">
        <Refresh />
      </Button>
    </form>
  )
})`
  display: flex;
  align-items: center;
  justify-content: space-between;

  output {
    color: var(--color-gray-90);
    font: var(--font-size-xl-semibold);
    width: 100%;
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
  }

  button {
    color: var(--color-gray-50);
    display: flex;
    padding: 0;
    margin-left: 20px;
    border-radius: 100%;
  }
`

const TotalSales = styled(({ className, value = 0, onRefreshClick, ...rest }) => {
  const { t } = useTranslation()

  return (
    <div {...rest} className={className} aria-label="total-sales">
      <small>{t('sold_this_month')}</small>
      <Form value={value} onRefreshClick={onRefreshClick} />
    </div>
  )
})`
  cursor: default;
  font-size: var(--text-size-sm);
  position: relative;
  display: flex;
  flex-direction: column;
  padding: var(--size-spacing-04);
  background-color: var(--color-white);

  small {
    color: var(--color-gray-70);
    font: var(--font-size-sm-semibold);
  }
`

TotalSales.displayName = 'TotalSales'

TotalSales.propTypes = {
  value: PropTypes.number,
}

export default TotalSales
